<template>
  <div class="login">
    <el-card class="xfn-login-card">
      <div slot="header"></div>
      <div>
        <el-form label-width="100px">

          <el-form-item label="">
            <el-input v-model="formData.aname" placeholder=""></el-input>
          </el-form-item>
        
          <el-form-item label="">
            <el-input type="password" v-model="formData.apwd" placeholder=""></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="doLogin"></el-button>
            <el-button @click="doCancel"></el-button>
          </el-form-item>
        
        </el-form>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data(){   //
    return {
      formData: { //
        aname: 'admin',
        apwd: '123456'
      }
    }
  },
  methods:{
    doLogin(){  //
      var url = this.$store.state.globalSettings.apiUrl+`/admin/login/${this.formData.aname}/${this.formData.apwd}`;

      this.$axios.get(url).then((res)=>{
        if(res.data.code==200){ //
          //Vuex
          this.$store.commit('setAdminName',this.formData.aname);
          //
          this.$router.push('/main');
        }else { //
          this.$alert('','',{type:'error'}).then(()=>{}).catch(()=>{});
        }
      }).catch((err)=>{
        console.log(err);
      })
    },
    doCancel(){ //
      this.formData.aname = '';
      this.formData.apwd = '';
    }
  }
}  
</script>

<style lang="scss">
  .xfn-login-card {
    width: 450px;
    margin: 100px auto;

    .el-card__header {
      text-align: center;
      font-size: 1.2em;
    }
  }
</style>
