<template>
  <div class="xfn-category-list">
    <el-breadcrumb>
      <el-breadcrumb-item to="/main"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <br>
    <el-button type="primary" @click="addCategory"></el-button>
    <br><br>
    <el-table :data="categoryList" stripe border>

      <el-table-column label="" prop="cid"></el-table-column>
    
      <el-table-column label="" prop="cname"></el-table-column>

      <el-table-column label="">
        <template slot-scope="{row,$index}">
          <el-button @click="updateCategory(row,$index)" type="success" size="mini"></el-button>
          <el-button @click="deleteCategory(row,$index)" type="danger" size="mini"></el-button>
        </template>
      </el-table-column>

    </el-table>
  </div>
</template>

<script>
export default {
  data(){
    return {
      categoryList:[]
    }
  },
  methods:{
    deleteCategory(c, i){
      this.$confirm('', '', {type:'warning'}).then(()=>{
        var url = this.$store.state.globalSettings.apiUrl + '/admin/category/'+c.cid;
      this.$axios.delete(url).then((res)=>{
        if(res.data.code==200){//
          this.categoryList.splice(i, 1);//
          this.$message.success('');
        }else {
          this.$message.error(''+res.data.msg);
        }
      }).catch((err)=>{
        console.log(err);
      })
      }).catch(()=>{

      })
    },
    updateCategory(c, i){
      this.$prompt('','',{
        inputValue: c.cname
      }).then(({value})=>{

      }).catch(()=>{

      })
    },
    addCategory(){
      this.$prompt('','',{type:'info'}).then(({value})=>{
        //API
        var url = this.$store.state.globalSettings.apiUrl + '/admin/category';
        this.$axios.post(url, {cname:value}).then((res)=>{
          if(res.data.code==200){
            //
            this.$message.success('!')   
            //
            this.categoryList.push({cid:res.data.cid, cname:value})
          }else {
            this.$message.error(''+res.data.msg);
          }
        }).catch((err)=>{
          console.log(err);
        })
      }).catch(()=>{
      })
    }
  },
  mounted(){
    var url = this.$store.state.globalSettings.apiUrl + '/admin/category';
    this.$axios.get(url).then((res)=>{
      this.categoryList = res.data;
    }).catch((err)=>{
      console.log(err);
    })
  }
}
</script> 
